:root{
    --mouse-x: 0px;
    --mouse-y: 0px;
}

.My-Page {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: whitesmoke;

    overflow-y: auto;
    z-index: 0;
}

.fade-enter {
    opacity: 0;
    z-index: 3;
}

.fade-enter-active {
    opacity: 1;
    transition: opacity 1000ms ease-in;
    z-index: 3;
}

.fade-exit {
    opacity: 1;
}

.fade-exit-active {
    opacity: 0;
    transition: opacity 1000ms ease-in;
}

.circle-fade-enter {
    clip-path: circle(0% at var(--mouse-x) var(--mouse-y));
    z-index: 3;
}

.circle-fade-enter-active {
    clip-path: circle(100%);
    transition: clip-path 1000ms ease-in;
    z-index: 3;
}

.circle-fade-exit {
    opacity: 1;
}

.circle-fade-exit-active {
    opacity: 0;
    transition: opacity 1000ms ease-in;
}


body {

}

.Header-image {
    object-fit: cover;
    position: relative;
    z-index: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 50vh;
}

.Right-section {
    position: absolute;

    top: 0vh;
    left: 50%;
    width: 50%;
    height: inherit;

    display: flex;
    justify-content: center;
    flex-direction: column;


}


.My-MD {

}

.MD-Image {
    max-width: 100%;
}

.Mask {
    z-index: 1;

    background-color: #aa2e25;
    opacity: 0.7;

    position: absolute;

    left: 50vw;
    top: 0;

    width: 70%;
    height: 100%;

    transform: skew(-20deg);
}

@media (prefers-reduced-motion: no-preference) {

}

.App-header {
    position: relative;

    top: 0;
    left: 0;

    height: 50vh;
    width: 100%;
    background-color: white;

    overflow: hidden;

}

.Header-text {
    position: relative;

    z-index: 2;

    color: white;
    font-size: calc(13px + 4vmin);

    margin-left: 10%;
}

.Sub-Header {
    position: relative;

    z-index: 2;

    color: white;
    font-size: calc(10px + 2vmin);

    margin-left: 10%;
}

.My-button {
    text-transform: none;
}

.My-Sticky{
    position: sticky;
    position: -webkit-sticky;
    top: 0;
}